<template>
    <div class="clProject">
        <!-- Header -->
        <el-row class="Header">
            <el-col :span="24">
                <div class="grid-content bg-purple-dark">
                    <div class="ss">
                        <div class="select-item">
                            <!-- 线路 -->
                            <p>路线：</p>
                            <el-dropdown class="select_" @command="changeLine">
                            <span class="el-dropdown-link">
                                {{save.line.routename}}<i class="el-icon-arrow-down el-icon--right"></i>
                            </span>
                            <el-dropdown-menu slot="dropdown">
                                <el-dropdown-item v-for="item in line" :key="item.id" :command="item">{{item.routename}}</el-dropdown-item>
                            </el-dropdown-menu>
                            </el-dropdown>
                            <!-- 站点 -->
                            <p style="margin-left: 20px;">站点：</p>
                            <el-dropdown class="select_"  @command="changeSite">
                            <span class="el-dropdown-link">
                                {{save.site.sitename}}<i class="el-icon-arrow-down el-icon--right"></i>
                            </span>
                            <el-dropdown-menu slot="dropdown">
                                <el-dropdown-item v-for="item in site" :key="item.id" :command="item">{{item.sitename}}</el-dropdown-item>
                            </el-dropdown-menu>
                            </el-dropdown>
                            <!-- 项目名称 -->
                            <p style="margin-left: 20px;">项目名称：</p>
                            <el-dropdown class="select_"  @command="changeProject">
                            <span class="el-dropdown-link">
                                {{save.projectMsg.pname || '未填写'}}<i class="el-icon-arrow-down el-icon--right"></i>
                            </span>
                            <el-dropdown-menu slot="dropdown">
                                <el-dropdown-item v-for="item in projectSelect" :key="item.id" :command="item">{{item.pname}}</el-dropdown-item>
                            </el-dropdown-menu>
                            </el-dropdown>
                        </div>
                        <div class="btn">
                            <el-button type="primary" @click="ctr('add')">新增项目</el-button>
                        </div>
                    </div>
                </div>
            </el-col>
        </el-row>
        <!-- Header END -->

        <div class="tableData">
            <div class="con">
                <div class="item" v-for="item in save.projectArr" :key="item.id">
                    <div class="box">
                        <div class="list_item">
                            <img src="@/assets/img/xiamgmumingc.png" alt="">
                            <p>{{item.pname}}</p>
                        </div>
                        <div class="list_item">
                            <img src="@/assets/img/shijian.png" alt="">
                            <p>{{item.ptime}}</p>
                        </div>
                        <div class="list_item">
                            <img src="@/assets/img/baojing.png" alt="">
                            <p>{{item.warningstatistics}}</p>
                        </div>
                        <div class="list_item">
                            <img src="@/assets/img/lg.png" alt="">
                            <p>{{item.lAltitude}}</p>
                        </div>
                        <div class="list_item">
                            <img src="@/assets/img/rg.png" alt="">
                            <p>{{item.rAltitude}}</p>
                        </div>
                        <div class="btn">
                            <div class="sub" @click="edit(item)">修改</div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="fy">
            <el-pagination
                background
                layout="prev, pager, next"
                :total="save.count">
            </el-pagination>
        </div>

        <div class="bg" v-show="save.status"></div>
        <div class="alert_con" v-show="save.status">
            <div class="title">{{title}}</div>
            <div class="item">
                <p>项目名称：</p>
                <el-input v-model="save.projectMsg.pname" placeholder="请输入内容"></el-input>
            </div>
            <div class="item">
                <p class="t">小车数据：</p>
                <el-dropdown class="select_" @command="changeXiaoCe">
                    <span class="el-dropdown-link">
                        {{save.xiaoceMsg}}<i class="el-icon-arrow-down el-icon--right"></i>
                    </span>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item v-for="item in xiaocheArr" :key="item.id" :command="item">{{item}}</el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>
            </div>
            <div class="btn">
                <el-button size="mini" @click="ctr(false)">取消</el-button>
                <el-button type="primary" size="mini" @click="ctr('ok')">确定</el-button>
            </div>
        </div>
    </div>
</template>
<script>
import api from "@/api/index.js";
export default {
    data () {
      return {
        title: '',
        page: '0',
        line: [],
        site: [],
        lineID: '',
        siteID: '',
        projectID: '',
        projectSelect: '',
        save: {
            status: false, // add edit
            line: {},
            site: {},
            projectMsg: {},
            projectArr: [],
            name: '',
            fileList: [],
            pageNum: 0,
            xiaoceMsg: ''
        },
        xiaocheArr: [], // 选择小车数据
        tableData: [],
        siteID: ''
      }
    },
    methods: {
        edit(val) {
            console.log(val);
            this.title = '修改';
            this.save.status = true;
            this.save.name = val.pname;
            this.save.projectMsg = val;
        },
        // 选择小车数据
        changeXiaoCe(val) {
            console.log('changeXiaoCe', val)
        },
        // 操作
        ctr(ctr) {
            console.log(ctr)
            console.log(this.save);
            this.save.status = ctr;
            if(this.title == '修改') {
                this.changeMeasurementprojectUpdatebyid(this.save.projectMsg);
                return;
            }
            switch(ctr) {
                case 'add':
                    this.title = '新增';
                    this.save.projectMsg = {
                        pname: ''
                    }
                    break;
                case 'ok':
                    console.log(this.save)
                    this.newMeasurementprojectInsert();
                    break;
            }
        },
        // 修改
        changeMeasurementprojectUpdatebyid(val) {
            api.changeMeasurementprojectUpdatebyid({
                tid: val.tid,
                lid: val.lid,
                turnoutstandardid: val.turnoutstandardid,
                pname: val.pname,
                ptime: val.ptime,
                warningstatistics: val.warningstatistics,
                id: val.id
            }).then(res => {
                console.log('修改', res);
                if(res.data > 0) {
                    this.$message({
                        showClose: true,
                        message: '项目新增成功',
                        type: 'success'
                    });
                    this.getMeasurementprojectSelectSelectBymid(this.siteID);
                } else {
                    this.$message({
                        showClose: true,
                        message: '项目修改失败',
                        type: 'warning'
                    })
                }
            })
        },
        // 改变线路
        changeLine (choose) {
            console.log(choose)
            this.save.line = choose;
            this.lineID = choose.id;
        },
        // 获取线路
        getSelectAll() {
            api.getSelectAll().then(res => {
                console.log('获取线路', res.data.list)
                this.line = res.data.list;
                this.save.line = res.data.list[0];
                this.lineID = res.data.list[0].id;
            })
        },
        // 改变站点
        changeSite(choose) {
            console.log('changeSite', choose);
            this.save.site = choose;
            this.siteID = choose.id;
        },
        // 改变项目
        changeProject(val) {
            console.log(val);
            this.projectID = val.id;
        },
        // 获取站点
        getLinesite(routeid) {
            api.getLinesite(routeid).then(res => {
                console.log('获取站点', res.data.list)
                this.site = res.data.list;
                this.save.site = res.data.list[0];
                this.siteID = res.data.list[0].id;
            })
        },
        // 检测项目 列表
        getMeasurementproject(id, pageNum) {
            api.getMeasurementproject(id, pageNum, 10).then(res => {
            console.log(res.data.list)
            this.tableData = res.data.list;
            })
        },
        // 新增项目
        newMeasurementprojectInsert() {
            api.newMeasurementprojectInsert({
                aid: this.save.userinfo.aid,
                tid: this.save.line.id,
                lid: this.save.site.id,
                pname: this.save.projectMsg.pname,
                file: this.save.xiaoceMsg
            }).then(res => {
                this.save.status = false;
                console.log(res);
                if(res.data.status == 'ok') {
                    this.$message({
                        showClose: true,
                        message: '项目新增成功',
                        type: 'success'
                    });
                    this.getMeasurementprojectSelectSelectBymid(this.siteID);
                } else {
                    this.$message({
                        showClose: true,
                        message: '项目新增失败',
                        type: 'warning'
                    });
                }
            })
        },
        // 项目列表
        getMeasurementprojectSelectSelectBy(id) {
            api.getMeasurementprojectSelectSelectBy(id, '', 0).then(res => {
                console.log('项目列表 下拉', res.data.list);
                this.projectSelect = res.data.list;
                this.save.projectMsg = {
                    pname: "请选择",
                    id: '-1'
                }
            })
        },
        // 项目列表
        getMeasurementprojectSelectSelectBymid(id) {
            api.getMeasurementprojectSelectSelectBymid(id, 0).then(res => {
                console.log('项目列表', res.data.list);
                this.save.projectArr = res.data.list;
                this.save.count = res.data.listCount;
            })
        },
        // 根据项目id查项目
        getMeasurementprojectSelectbyid(id) {
            api.getMeasurementprojectSelectbyid(id).then(res => {
                console.log('根据项目id查项目', res);
                var arr = [];
                arr[0] = res.data;
                this.save.projectArr = arr;
                console.log(this.save)
            })
        },
        // 获取小车数据
        getCalculationstandardCar() {
            api.getCalculationstandardCar().then(res => {
                console.log('获取小车数据',res.data.result);
                this.xiaocheArr = res.data.result;
                this.save.xiaoceMsg = res.data.result[0];
            })
        }
    },
    watch: {
        lineID(val) {
            console.log('lineID', val)
            this.getLinesite(val)
        },
        siteID(val) {
            console.log('siteID', val);
            console.log('save', this.save)
            this.getMeasurementprojectSelectSelectBymid(val);
            this.getMeasurementprojectSelectSelectBy(val);
        },
        projectID(val) {
            console.log('projectID', val);
            this.getMeasurementprojectSelectbyid(val);
        }
    },
    created() {
        this.getSelectAll();
        if(localStorage.getItem('USERINFO')) {
            this.save.userinfo = JSON.parse(localStorage.getItem('USERINFO'))
        }
        this.getCalculationstandardCar();
    },
}
</script>
<style lang="stylus" scoped>
.Header 
    color #fff
    display flex
    align-items center
    .select-item
        display flex
        align-items center
        color #fff
        .el-dropdown-link
            color #fff
    .ss
        display flex
        align-items center
        justify-content space-between
        padding-right 110px
.top_bt 
    display flex
    align-items center
    color #fff
    p
        margin-left 30px
.tableData
    padding 20px 100px 20px 40px
    .con 
        font-size 0
        .item
            width 20%
            height 370px
            padding 10px
            box-sizing border-box
            display inline-block
            font-size 16px
            .box
                background #fff
                width 100%
                height 100%
                border-radius 4px 
                padding-top 30px
                box-sizing border-box
                .list_item 
                    display flex
                    align-items center
                    padding 10px 0 10px 40px
                    box-sizing border-box
                    img 
                        width 30px
                        margin-right 6px
                .btn 
                    display flex
                    justify-content center
                    .sub
                        width 120px
                        background #3180ff
                        text-align center
                        padding 6px 0
                        border-radius 20px
                        color #fff
                        font-size 14px
                        margin-top 16px
                        cursor pointer
.fy
    text-align center
.bg
    width 100%
    height 100vh
    background rgba(0,0,0,.5)
    position fixed
    top 0
    left 0
    z-index 900
.alert_con
    width 400px
    background #fff
    position fixed
    left 50%
    top 50%
    transform translate(-50%, -50%)
    z-index 999
    padding 20px
    border-radius 4px
    .title 
        padding-bottom 20px
    .item 
        display flex 
        align-items center
        white-space nowrap
        padding 10px 0
    .btn
        display flex
        justify-content flex-end
        padding-top 30px

</style>



